        <div class="card-body">
          <h2>Ausschreibung JSM 2019</h2>
          <hr>
          <ol>
            <h2><li> Zweck </li></h2>
            <p>Die Junioren Schweizermeisterschaft auf dem Flugplatz Dittingen ist ein Wettbewerb
            im Leistungssegelflug. Er dient der Ermittlung einer Schweizermeisterin, eines
            Schweizermeisters der Junioren. Der Vorstand des SFVS ist verantwortlich fr die
            Organisation der JSM. Er bertrgt die Durchfhrung der Meisterschaft in diesem
            Jahr an die Segelfluggruppe Dittingen.</p>

            <h2><li> Veranstalter </li></h2>
            <p>Segelfluggruppe Dittingen <br>
            Flugplatz <br>
            4243 Dittingen <br>
            OK Prsident: Florian Anklin <br>
            Konkurrenzleitung: Beat Scherrer <br>
            Web: <a href="#">www.jsm2019.ch</a> <br>
            E-Mail: <a href="#">mail@jsm2019.ch</a> <br>
            IBAN: CH12 8080 8004 1710 5382 0</p>


            <h2><li> Ort </li></h2>
            <p>Der Wettbewerb findet auf dem Flugplatz Dittingen (LSPD) statt. Das
            Wettbewerbsgebiet umfasst die Gebiete der ICAO-Karte Schweiz und Deutschland,
            Blatt Stuttgart. Geflogen wird voraussichtlich im Jura, im Schwarzwald auf der
            Schwbischen Alb und in den Vogesen.</p>

            <h2><li> Reglemente </li></h2>
            <p>Die Segelflug Schweizermeisterschaft der Junioren wird gemss JSM Reglement
            des Schweizer Segelflugverbandes ausgeschrieben. Mitgeltend sind demnach
            auch das RM Reglement und das SM Reglement mit allen Anhngen. <br>
            Der Wettbewerb wird auf Grundlage der zum Zeitpunkt der Ausschreibung gltigen
            Reglemente durchgefhrt:
            <ul>
              <li>JSM Reglement des Schweizer Segelflugverbandes</li>
              <li>FAI Sporting Code Section 3</li>
            </ul>
            Lokale Verfahren, insbesondere die Regelung des Endanfluges, behlt sich der
            Ausrichter vor. Sie werden am Erffnungsbriefing bekannt gegeben.</p>

            <h2><li> Voraussetzungen fr die Teilnahme </li></h2>
            Fr eine Teilnahme an der JSM sind erforderlich:
            <ul>
              <li>Gltige Segelflug- und Sport-Lizenz</li>
              <li>Bezahltes Nenngeld</li>
              <li>Mitgliedschaft einer Segelfluggruppe des SFVS</li>
              <li>Am 31.12.2019 noch nicht 25 Jahre alt</li>
              <li>Standartklasse-Segelflugzeug (es wird mit Index gewertet)</li>
              <li>Gltige Bordpapiere inklusive Haftpflichtversicherung und Certificate of
                Insurance</li>
              <li>FLARM bitte mit Range Analyzer Results</li>
            </ul>
            <p>Nenngeld JSM: CHF 80.-<br>
            Aufgrund der Platzverhltnisse ist die Teilnehmerzahl auf 25 Piloten begrenzt, die
            Bercksichtigung erfolgt nach Anmeldungseingang. <br>
            Versicherung ist Sache der Teilnehmer.</p>

            <h2><li> Dokumente </li></h2>
            <p>Fr die Teilnahme am Wettbewerb sind gltige Dokumente zwingend notwendig. Wir
            erlauben uns Stichproben durchzufhren.</p>

            <h2><li> Flugauswertung </li></h2>
            <p>Jeder Pilot ist fr das Auslesen und bermitteln seiner Loggerdaten selber
            verantwortlich.</p>

            <h2><li> Termine </li></h2>
            <table class="table">
              <tbody>
                <tr>
                  <th scope="col">Datum</th>
                  <th scope="col">Zeit</th>
                  <td></td>
                </tr>
                <tr>
                  <th scope="row">31.03.2019</th>
                  <td></td>
                  <td>Anmeldeschluss</td>
                </tr>
                <tr>
                  <th scope="row">29. & 30.04.2019</th>
                  <td></td>
                  <td>Freies Training</td>
                </tr>
                <tr>
                  <th scope="row">30.04.2019</th>
                  <td></td>
                  <td>Anmeldung im Wettbewerbsbro</td>
                </tr>
                <tr>
                  <td></td>
                  <td>20:00</td>
                  <td>Erffnungsbriefing</td>
                </tr>
                <tr>
                  <th scope="row">ab 01.05.2019</th>
                  <td>10:00</td>
                  <td>Briefing</td>
                </tr>
                <tr>
                  <th scope="row">03.05.2019</th>
                  <td>17:00</td>
                  <td>Sponsoren Apro</td>
                </tr>
                <tr>
                  <th scope="row">05.05.2019</th>
                  <td>ca. 19:00</td>
                  <td>Siegerehrung und Nachtessen</td>
                </tr>
              </tbody>
            </table>
            <p>Die Anmeldung im Wettbewerbsbro und die Teilnahme bei den Briefings sind
            obligatorisch. Kurzfristige Zeitnderungen werden auf den diversen Kanlen
            verffentlicht.</p>

            <h2><li> Anfahrt / Parking </li></h2>
            <p>Die Anfahrt erfolgt ber die normale Zufahrt zum Flugplatz. Bitte beachtet die
            Haarnadelkurve im Dorf mit Grenzstein auf der linken Strassenseite. Die
            Anhngerpltze werden zugeteilt. Besucher benutzen die beschilderten
            Besucherparkpltze.</p>

            <h2><li> Freies Training </li></h2>
            <p>Sollten sich einige Piloten dafr anmelden besteht die Mglichkeit zum freien
            Training am 29. & 30.April 2019.</p>

            <h2><li> Groundhandling </li></h2>
            <p>Die Platzverhltnisse auf dem Flugplatz sind sehr beschrnkt. Gegenseitige
            Rcksichtnahme und Hilfsbereitschaft sind Voraussetzung fr einen reibungslosen
            Betrieb. <br>
            Die Segelflugzeuge werden mit Hilfe der SGD-Helfer und SGD-Fahrzeugen ins Grid
            gezogen. Keine Privaten PWs auf der Piste!</p>

            <h2><li> Rckholservice </li></h2>
            <p>Fr das Rckholen nach eventuellen Aussenlandungen im Feld bieten wir einen
            Rckholservice an. Wir vermitteln nur den Rckholer. Das Fahrzeug ist durch den
            Teilnehmenden zu stellen und muss, wie auch der Anhnger, in einem einwandfreien
            und abfahrbereiten Zustand sein. <br>
            Wer diesen Dienst beanspruchen mchte, bezahlt pauschal und im Voraus CHF
            100.-. Es fallen keine weiteren Kosten an. Wurde der Dienst whrend der JSM nie
            bentigt wird der volle Betrag zurckerstattet. Der Organisator lehnt jede Haftung
            fr Schden im Zusammenhang mit dem Service ab.</p>

            <h2><li> Camping </li></h2>
            <p>Von Sonntag 28. April bis Montag 6. Mai 2018 besteht die Mglichkeit fr Piloten und
            Helfer auf dem Flugplatz Dittingen zu campieren. Die Kosten fr das Campieren sind
            bis zum 28.02.2019 im Nenngeld inbegriffen (Anschliessend 20.-). Es stehen WCs
            und Duschen zur Verfgung.</p>

            <h2><li> Verpflegung </li></h2>
            <p>Im Clubhaus der SGD wird es von 08:00 bis 10:00 ein Frhstcksbuffet geben.
            Durch den Tag bieten wir Sandwiches und Grilladen an. Abends wird jeweils ein
            warmes Abendessen zum Selbstkostenpreis angeboten.</p>
          </ol>
        </div>
      </div>
    </div>
